import {Component,Injectable,NgZone} from '@angular/core';
import {HTTP_PROVIDERS} from '@angular/http';// directly import all providers http is dependent upon it itself is an array
import {OnInit} from '@angular/core';//A lifecycle hook
import {RouterLink} from '@angular/router';
import { ROUTER_DIRECTIVES,Router, RouterConfig,ActivatedRoute} from '@angular/router';
import {FORM_DIRECTIVES,FormBuilder, ControlGroup, Control, Validators} from '@angular/common';
import {PeronalDetails} from './personaldetails.form.ts';
import {PersonalDetailsService} from './personaldetails.service';
// webpack html imports
let template = require('./personaldetails.component.html');

@Component({
    selector:'personal-details',

    template:template,
    providers:[HTTP_PROVIDERS,PersonalDetailsService],
    styles:[require('./personaldetails.component.css')],
    directives: [RouterLink,ROUTER_DIRECTIVES]
    
})

@Injectable()
export class PersonalDetailsComponent implements OnInit{

    
    form:ControlGroup;
    private _personalDetailsService:PersonalDetailsService;
    constructor(private router:Router,private route:ActivatedRoute, fb:FormBuilder, private _pService:PersonalDetailsService){
       
        this.form = fb.group({
           name:['',Validators.required],
           callingName:['',Validators.required],
           fatherName:['',Validators.required],
           empId:['',Validators.required],
           cnicNo:['',Validators.required],
           cnicExpiry:['',Validators.required],
           bloodGroup:['',Validators.required],
           dateOfBirth:['',Validators.required],
           gender:['',Validators.required],
           maritalStatus:['',Validators.required],
           religion:['',Validators.required],
           nationality:['',Validators.required],
           smoking:['',Validators.required],
           passportNo:['',Validators.required],
           passportExpiry:['',Validators.required],
           drvLicense:['',Validators.required],
           drvLicenseExp:['',Validators.required]
        });
        this._personalDetailsService=_pService;
       
    }

    
    
   setForm(){console.log(this.form.value.name);}

    ngOnInit(){//ITS CALLED AFTER CONSTRUCTOR, WE DONOT DO HEAVY OPERATIONS IN
                //CONSTRUCTOR SO THAT LOADING DOESNOT BECOME HEAVY
              // this._userService.getUsers().subscribe(users => this.users = users);

              
              var tempForm=this._personalDetailsService.get();
              for(var key in this.form.controls)
               {(<Control>this.form.controls[key]).updateValue(tempForm[key])}
                //this.form.controls['name'].updateValueAndValidity("|");
               // console.log(this.form.value);
                //=this._personalDetailsService.get();

    }

    
}


