import {Component,OnInit} from '@angular/core';
import {CORE_DIRECTIVES, FORM_DIRECTIVES, NgClass} from '@angular/common';
import {PreviewService} from './previewservice';
import {ROUTER_DIRECTIVES, Router, RouterConfig} from '@angular/router';
import {isRouteActive} from '../myrouter';//for checking whtether passed uri is active

// webpack html imports
let template = require('./previewcontainer.component.html');

@Component({
  selector: 'preview-container',
  template: template,
  directives: [NgClass, CORE_DIRECTIVES, FORM_DIRECTIVES],
  styles: [require('./previewcontainer.component.css')],
  providers: [PreviewService]
  })

export class PreviewContainerComponent implements OnInit{

  public router:Router; 
  public input:any;

  private _previewService:PreviewService;

  constructor(private _pService:PreviewService,private _router: Router){
      this._previewService=_pService;
      this.router=_router;
  }


  setData() {
        (<HTMLInputElement>document.getElementById("profilePic")).src=this.input.image;
        (<HTMLInputElement>document.getElementById("previewIcon")).src="../../public/images/review.png";
        (<HTMLInputElement>document.getElementById("profileIcon")).src="../../public/images/profile.png";
        (<HTMLInputElement>document.getElementById("previewHeading")).innerText = this.input.heading;
        (<HTMLInputElement>document.getElementById("previewDetail")).innerText = this.input.detail;
  }

  ngOnInit(){

        
        this.input=this._previewService.get();
        this.setData();  
        
 }

  goto(){
        this.router.navigate([this.input.link]);
    }
  
}




